@import 'theme';
@import 'mixins';

.risks-detail {
  width: 100%;
  background: $color-card-bg-active;
  box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
  border-radius: 10px;
  padding: 20px 40px;
}
.risk-title {
  margin-bottom: 20px;
  color: $color-text-default;
}
.risk-card {
  background-color: $color-info-card-bg;
  @include lightThemeOnly {
    border: 1px solid $color-border-secondary;
  }
  border-radius: 10px;
  padding: 16px;
  .sun-text-medium {
    margin-bottom: 16px;
    color: $color-text-default;
    position: relative;
    padding-left: 15px;
    &:before {
      content: '';
      width: 8px;
      height: 8px;
      position: absolute;
      left: 0;
      top: 7px;
      background: $color-primary;
      border-radius: 50%;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .risk-content-4 {
    padding-left: 0;
    &:before {
      display: none;
    }
  }
}
