@import '../../../assets/css/mixins';
.page_title {
  font-size: 39px;
  line-height: 39px;
  margin-bottom: 18px;
  font-family: Helvetica-Bold, Helvetica;
  &_wrap {
    margin-bottom: 40px;
  }
  &_ele {
    @include darkThemeOnly {
      background: linear-gradient(180deg, #ffffff 0%, #c5aaff 100%);
      background-clip: text;
    }
    @include lightThemeOnly {
      background: linear-gradient(180deg, #bfa0ff 0%, #5100ff 100%);
      background-clip: text;
    }
    color: transparent;
  }
  &_sub {
    font-size: 16px;
    font-weight: 400;
    @include darkThemeOnly {
      color: rgba(255, 255, 255, 0.5);
    }
    @include lightThemeOnly {
      color: rgba(#333, 0.5);
    }
    line-height: 22px;
  }
  &_revert {
    margin-bottom: 0;
    margin-top: 18px;
  }
}
@media screen and (max-width: 899px) {
  .page_title {
    font-size: 30px;
    line-height: 32px;
    margin-bottom: 16px;
    &_wrap {
      margin-bottom: 30px;
    }
    &_sub {
      font-size: 12px;
      line-height: 17px;
    }
  }
}
