// 渐变边框 mixin
@mixin withLinearGradientBorder($gradient, $width: 1px) {
  position: relative;
  z-index: 0;

  &::before {
    content: "";
    position: absolute;
    inset: 0; // top:0; right:0; bottom:0; left:0;
    border-radius: inherit;
    padding: $width; // 控制边框宽度
    background: $gradient;
    -webkit-mask: linear-gradient(#fff 0 0) content-box,  linear-gradient(#fff 0 0); // 中间挖空
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    pointer-events: none;
    z-index: -1;
  }
}

// --------------------------------------------------------
// 渐变边框 + 高斯模糊背景 玻璃拟态通用 mixin
// --------------------------------------------------------
// 参数说明：
// $border-gradient   → 边框渐变
// $border-width      → 边框宽度
// $border-radius     → 圆角
// $bg-color          → 内部背景色（可含透明度）
// $blur              → 模糊强度
// --------------------------------------------------------
@mixin gradient-glass-border(
  $border-gradient: linear-gradient(180deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05)),
  $border-width: 1px,
  $border-radius: 12px,
  $bg-color: rgba(21, 12, 40, 0.4),
  $blur: 10px
) {
  position: relative;
  border-radius: $border-radius;
  overflow: hidden;
  z-index: 0;

  // 渐变边框层
  &::before {
    content: "";
    position: absolute;
    inset: 0;
    padding: $border-width;
    border-radius: inherit;
    background: $border-gradient;

    // 关键：mask 出中间区域，只保留边框
    -webkit-mask:
            linear-gradient(#fff 0 0) content-box,
            linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor; /* Safari */
    mask-composite: exclude;     /* Chrome/Edge */
    z-index: 1;
  }

  // 模糊+背景层
  &::after {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: inherit;
    background: $bg-color;
    backdrop-filter: blur($blur);
    z-index: 0;
  }

  // 内容保持在最上层
  > * {
    position: relative;
    z-index: 2;
  }
}
